export default {
	props: {
		type: {
			type: String,
			default: 'default'
		},
		value: {
			type: Boolean,
			default: false
		},
		maskEnable: {
			type: Boolean,
			default: true
		},
		mData: {
			type: [Object, Array],
			default: () => {}
		},
		nav: {
			type: Boolean,
			default: true
		},
		navMask: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			navList: [],
			tabbarList: []
		}
	},
	mounted() {



	},
	methods: {
		createCommodity(choice) {
			this.$emit('onConfirm', choice)
			this.$emit('input', false)
		},
		inputConfirm() {
			this.$emit('onConfirm', this.mData.content)
			this.$emit('input', false)
		},
		tapCancel() {
			this.$emit('onCancel')
			this.$emit('input', false)
		},
		tapConfirm(item) {
			this.$emit('onConfirm', item)
			this.$emit('input', false)
		},
		tapMask() {
			if (!this.maskEnable) return
			this.$emit('input', false)
		},
		creatPlusMask(navHeight, tabbarHeight, opacity) {
			for (let i = 1; i <= 10; i++) {
				let navView = new plus.nativeObj.View('nav', {
					top: '0px',
					left: '0px',
					height: `${navHeight}px`,
					width: '100%'
				});
				navView.draw([{
					tag: 'rect',
					id: 'rect',
					color: `rgba(0,0,0,0.1)`,
					position: {
						top: '0px',
						left: '0px',
						width: '100%',
						height: '100%'
					}
				}, ]);
				navView.addEventListener("click", (e) => {
					this.tapMask()
				}, false);
				let tabbarView = new plus.nativeObj.View('tabbar', {
					bottom: '0px',
					left: '0px',
					height: `${tabbarHeight}px`,
					width: '100%'
				});
				tabbarView.draw([{
					tag: 'rect',
					id: 'rect',
					color: `rgba(0,0,0,0.1)`,
					position: {
						top: '0px',
						left: '0px',
						width: '100%',
						height: '100%'
					}
				}, ]);
				tabbarView.addEventListener("click", (e) => {
					this.tapMask()
				}, false);
				this.navList.push(navView)
				this.tabbarList.push(tabbarView)
			}

		},
		async init() {
			let promise = new Promise((resolve, reject) => {
				uni.getSystemInfo({
					success: function(e) {
						let customBar
						customBar = e.statusBarHeight + 44;
						resolve(customBar)
					}
				})
			})
			let navHeight = await promise
			navHeight = this.nav ? navHeight : 0
			let tabbarHeight = uni.getSystemInfoSync().screenHeight - uni.getSystemInfoSync().windowHeight - navHeight
			this.creatPlusMask(navHeight, tabbarHeight, 0.6)



		},
		showPlusMask() {
			let i = 0
			const temp = setInterval(() => {
				if (i == 9) {
					clearInterval(temp)
				} else {
					this.navList[i].show()
					this.tabbarList[i].show()
					// if(i!=0) this.navList[i-1].hide()
					// if(i!=0) this.tabbarList[i-1].hide()
					i++
				}
			}, 30)
		}
	},
	watch: {
		value: {
			immediate: true,
			handler(newVal, oldVal) {
				if (!this.navMask) return
















			}
		}
	}
}
